import { useState } from "react";
import "./styles.css";

function secretKey(env, key) {
  return `pcapi-${env}_${key.toLowerCase()}`;
}
//onClick={}}
const TextWithCopy = ({ value, ...rest }) => (
  <div {...rest}>
    {value}
    <button
      style={{ marginLeft: 10 }}
      onClick={() => navigator.clipboard.writeText(value)}
    >
      copy
    </button>
  </div>
);

export default function App() {
  const [key, setKey] = useState("");
  return (
    <div className="App">
      <input
        style={{ width: 300, marginBottom: 30, marginTop: 20 }}
        value={key}
        onChange={(e) => setKey(e.target.value)}
        placeholder="GAGNANT_DE_SECRET_STORY"
      />
      {key ? (
        <>
          <TextWithCopy
            value={secretKey("testing", key)}
            style={{ marginTop: 10 }}
          />
          <TextWithCopy
            value={secretKey("staging", key)}
            style={{ marginTop: 10 }}
          />
          <TextWithCopy
            value={secretKey("integration", key)}
            style={{ marginTop: 10 }}
          />
          <TextWithCopy
            value={secretKey("production", key)}
            style={{ marginTop: 10 }}
          />
        </>
      ) : (
        <div>{"^ paste secret key here"}</div>
      )}
    </div>
  );
}
